import React from 'react';
import { Button } from 'reactstrap';

class List extends React.Component{
    render(){
        const {value, onClick} = this.props;
        return(
            <ul>
                <li className="list">{this.props.item}<Button style={{color: '#495057', backgroundColor: '#e9ecef', fontSize: '14px'}} onClick={()=>onClick(value)} value={value} aria-label="delete" type="button" className="delete">Delete</Button></li>
            </ul>
        )
    }
}
export default List;



